.social__lists {
  .list-reset();
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 13px;
  padding-bottom: 13px;
  border-top: 1px solid @special-other-grey;
  border-bottom: 1px solid @special-other-grey;
}

.social__item {
  padding: 12px;
}

.social__item + .social__item {
  margin-left: 2px;
}

.social__link {
  display: block;
  width: 132px;
}

.social__link--vkontakte {
  width: 28px;
  height: 22px;
}

.social__link--youtube {
  width: 28px;
  height: 22px;
}

.social__link--telegram {
  width: 28px;
  height: 22px;
}

.social__link-icon {
  fill: @special-black;
}

.social__link:hover .social__link-icon {
  fill: @basic-green;
}

.social__link:disabled.social__link-icon {
  fill: @basic-green;
  opacity: 0.3;
}

@media (min-width: @tablet-width) {
  .social__lists {
    .list-reset();
    display: flex;
    flex-direction: row;
    align-self: center;
    border-top: none;
    border-bottom: none;
  }

