.maim-nav__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  box-sizing: border-box;
}
.main-nav {
  position: relative;
  width: auto;
  min-height: 65px;
}
.main-nav__toggle {
  padding: 0;
  position: absolute;
  top: -56px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 35px;
  height: 50px;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
.main-nav--closed .main-nav__toggle::before,
.main-nav--closed .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  right: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
}
.main-nav--closed .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--closed .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 24px;
    right: 0;
    z-index: 10;
    min-height: 60vh;
    background-color: #ffffff;
  }
  .main-nav--opened .main-nav__wrapper::before {
    content: "";
    position: absolute;
    top: 20px;
    right: 0;
    z-index: 2;
    width: 24px;
    height: 100%;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 18px;
  right: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    margin-top: 30px;
